"use client";
import { useState, useEffect } from "react";
import { BiSearch, BiQr } from "react-icons/bi";
import { FiTag, FiCopy } from "react-icons/fi";
import useAdminStore from "@/store/adminStore";
import toast from "react-hot-toast";

export default function CouponDataTable() {
  const { coupons, isLoading, getCoupons } = useAdminStore();
  const [searchTerm, setSearchTerm] = useState("");
  const [entriesPerPage, setEntriesPerPage] = useState(10);
  const [currentPage, setCurrentPage] = useState(1);

  useEffect(() => {
    const fetchCoupons = async () => {
      try {
        await getCoupons({ limit: 100 });
      } catch (error) {
        console.error("Failed to load coupons:", error);
        toast.error("Failed to load coupons");
      }
    };
    fetchCoupons();
  }, [getCoupons]);

  const copyCouponCode = (code) => {
    navigator.clipboard.writeText(code);
    toast.success("Coupon code copied!");
  };

  const filteredCoupons = coupons.filter(coupon =>
    coupon.code.toLowerCase().includes(searchTerm.toLowerCase()) ||
    coupon.couponFor.toLowerCase().includes(searchTerm.toLowerCase())
  );

  const indexOfLastEntry = currentPage * entriesPerPage;
  const indexOfFirstEntry = indexOfLastEntry - entriesPerPage;
  const currentEntries = filteredCoupons.slice(indexOfFirstEntry, indexOfLastEntry);
  const totalPages = Math.ceil(filteredCoupons.length / entriesPerPage);

  const paginate = (pageNumber) => setCurrentPage(pageNumber);

  return (
    <>
      <div className="mb-8">
        <h1 className="text-4xl font-bold text-gray-900 mb-2">Coupon Generated Data</h1>
        <nav className="text-gray-500 text-sm">
          <ol className="flex space-x-2 items-center">
            <li>
              <a href="/admin" className="hover:underline text-gray-600 hover:text-blue-600">
                Home
              </a>
            </li>
            <li className="text-gray-400">/</li>
            <li>
              <a href="/admin/coupons" className="hover:underline text-gray-600 hover:text-blue-600">
                Coupon Data
              </a>
            </li>
            <li className="text-gray-400">/</li>
            <li className="text-gray-700">All Coupons</li>
          </ol>
        </nav>
      </div>

      <div className="bg-white rounded-xl shadow-sm border border-gray-100 p-6">
        <h2 className="text-xl font-semibold text-blue-900 mb-6">Coupon Data Table</h2>

        <div className="flex items-center justify-between mb-6">
          <div className="flex items-center gap-2">
            <select
              value={entriesPerPage}
              onChange={(e) => {
                setEntriesPerPage(Number(e.target.value));
                setCurrentPage(1);
              }}
              className="border border-gray-300 rounded-lg px-3 py-2 text-sm focus:outline-none focus:ring-2 focus:ring-blue-500"
            >
              <option value={10}>10</option>
              <option value={25}>25</option>
              <option value={50}>50</option>
              <option value={100}>100</option>
            </select>
            <span className="text-sm text-green-600 font-medium">entries per page</span>
          </div>

          <div className="relative">
            <input
              type="text"
              placeholder="Search..."
              value={searchTerm}
              onChange={(e) => {
                setSearchTerm(e.target.value);
                setCurrentPage(1);
              }}
              className="border border-gray-300 rounded-lg pl-4 pr-10 py-2 text-sm focus:outline-none focus:ring-2 focus:ring-blue-500 w-64"
            />
            <BiSearch className="absolute right-3 top-1/2 transform -translate-y-1/2 text-gray-400" size={20} />
          </div>
        </div>

        {isLoading ? (
          <div className="text-center py-12">
            <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-green-500 mx-auto"></div>
            <p className="text-gray-600 mt-4">Loading coupons...</p>
          </div>
        ) : (
          <>
            <div className="overflow-x-auto">
              <table className="w-full">
                <thead>
                  <tr className="bg-gradient-to-r from-green-50 to-blue-50 border-b-2 border-green-200">
                    <th className="px-4 py-3 text-left text-sm font-semibold text-gray-700">Coupon Code</th>
                    <th className="px-4 py-3 text-left text-sm font-semibold text-gray-700">Type</th>
                    <th className="px-4 py-3 text-left text-sm font-semibold text-gray-700">Discount</th>
                    <th className="px-4 py-3 text-left text-sm font-semibold text-gray-700">Partner</th>
                    <th className="px-4 py-3 text-left text-sm font-semibold text-gray-700">Expiry Date</th>
                    <th className="px-4 py-3 text-left text-sm font-semibold text-gray-700">Status</th>
                    <th className="px-4 py-3 text-left text-sm font-semibold text-gray-700">Usage</th>
                  </tr>
                </thead>
                <tbody>
                  {currentEntries.length > 0 ? (
                    currentEntries.map((coupon, index) => (
                      <tr
                        key={coupon._id}
                        className={`border-b border-gray-100 hover:bg-gray-50 transition-colors ${
                          index % 2 === 0 ? 'bg-white' : 'bg-gray-50/30'
                        }`}
                      >
                        <td className="px-4 py-4">
                          <div className="flex items-center gap-2">
                            <FiTag className="text-green-600" size={16} />
                            <span className="font-mono font-semibold text-gray-900">{coupon.code}</span>
                            <button 
                              onClick={() => copyCouponCode(coupon.code)}
                              className="text-gray-400 hover:text-green-600 transition-colors"
                              title="Copy code"
                            >
                              <FiCopy size={14} />
                            </button>
                          </div>
                        </td>
                        <td className="px-4 py-4">
                          <span className={`px-3 py-1 rounded-full text-xs font-medium ${
                            coupon.type === 'food' ? 'bg-orange-100 text-orange-700' :
                            coupon.type === 'health' ? 'bg-blue-100 text-blue-700' :
                            'bg-purple-100 text-purple-700'
                          }`}>
                            {coupon.type}
                          </span>
                        </td>
                        <td className="px-4 py-4">
                          <span className="font-medium text-gray-900">
                            {coupon.discountType === 'percentage' 
                              ? `${coupon.discountValue}%` 
                              : `â‚¹${coupon.discountValue}`}
                          </span>
                        </td>
                        <td className="px-4 py-4 text-sm text-gray-700">
                          {coupon.partner?.businessName || coupon.partner?.name || 'N/A'}
                        </td>
                        <td className="px-4 py-4 text-sm text-gray-700">
                          {new Date(coupon.expiryDate).toLocaleDateString()}
                        </td>
                        <td className="px-4 py-4">
                          <span className={`px-3 py-1 rounded-full text-xs font-medium ${
                            coupon.isActive && new Date(coupon.expiryDate) > new Date() 
                              ? 'bg-green-100 text-green-700' 
                              : 'bg-red-100 text-red-700'
                          }`}>
                            {coupon.isActive && new Date(coupon.expiryDate) > new Date() ? 'Active' : 'Inactive'}
                          </span>
                        </td>
                        <td className="px-4 py-4">
                          <div className="text-xs text-gray-600">
                            <div>Used: {coupon.usedCount || 0}</div>
                            {coupon.maxUses && <div>Max: {coupon.maxUses}</div>}
                          </div>
                        </td>
                      </tr>
                    ))
                  ) : (
                    <tr>
                      <td colSpan="7" className="px-4 py-12 text-center text-gray-500">
                        <div className="text-4xl mb-2">ðŸŽ«</div>
                        {isLoading ? 'Loading coupons...' : 'No coupons found'}
                      </td>
                    </tr>
                  )}
                </tbody>
              </table>
            </div>

            <div className="flex items-center justify-between mt-6">
              <div className="text-sm text-gray-600">
                Showing {indexOfFirstEntry + 1} to {Math.min(indexOfLastEntry, filteredCoupons.length)} of {filteredCoupons.length} entries
              </div>

              <div className="flex items-center gap-2">
                {Array.from({ length: Math.min(totalPages, 10) }, (_, i) => i + 1).map((page) => (
                  <button
                    key={page}
                    onClick={() => paginate(page)}
                    className={`px-3 py-1 rounded-md text-sm font-medium transition-colors ${
                      currentPage === page
                        ? 'bg-gray-200 text-gray-900'
                        : 'text-gray-600 hover:bg-gray-100'
                    }`}
                  >
                    {page}
                  </button>
                ))}
                {totalPages > 10 && (
                  <button
                    onClick={() => paginate(Math.min(currentPage + 1, totalPages))}
                    disabled={currentPage === totalPages}
                    className="px-3 py-1 text-sm text-gray-600 hover:text-gray-900 disabled:opacity-50 disabled:cursor-not-allowed"
                  >
                    â€º
                  </button>
                )}
              </div>
            </div>
          </>
        )}
      </div>
    </>
  );
}



